.select-group {
  margin-bottom: 1rem;
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}

.vpfo-find-spaces-table {
  width: fit-content;
  margin: 0 auto;
}
.vpfo-find-spaces-table tbody {
  padding: 12px;
}
.vpfo-find-spaces-table th,
.vpfo-find-spaces-table td {
  border: 1px solid #ccc;
  padding: 12px;
}